'use client'

import { useEffect, useState } from 'react'
import dynamic from 'next/dynamic'
import { Button } from '@/components/ui/button'

const GameBoard = dynamic(() => import('@/components/GameBoard'), { 
  ssr: false 
})

export default function Home() {
  const [showInstallPrompt, setShowInstallPrompt] = useState(false)
  const [deferredPrompt, setDeferredPrompt] = useState<any>(null)

  useEffect(() => {
    const handleBeforeInstallPrompt = (e: Event) => {
      e.preventDefault()
      setDeferredPrompt(e)
      setShowInstallPrompt(true)
    }

    window.addEventListener('beforeinstallprompt', handleBeforeInstallPrompt)

    return () => {
      window.removeEventListener('beforeinstallprompt', handleBeforeInstallPrompt)
    }
  }, [])

  const handleInstall = async () => {
    if (deferredPrompt) {
      deferredPrompt.prompt()
      const { outcome } = await deferredPrompt.userChoice
      if (outcome === 'accepted') {
        setShowInstallPrompt(false)
      }
      setDeferredPrompt(null)
    }
  }

  return (
    <main className="relative min-h-screen bg-gradient-to-br from-blue-200 via-purple-200 to-pink-200">
      {showInstallPrompt && (
        <div className="fixed bottom-4 left-4 right-4 bg-white rounded-lg p-4 shadow-lg z-50 md:w-96 md:left-1/2 md:-translate-x-1/2">
          <h3 className="text-lg font-semibold mb-2">Install Bubble Pop Adventure</h3>
          <p className="mb-4 text-gray-600">
            Install this game on your device for the best experience!
          </p>
          <div className="flex gap-2">
            <Button onClick={handleInstall} className="flex-1 bg-blue-500 hover:bg-blue-600">
              Install Now
            </Button>
            <Button 
              variant="outline" 
              onClick={() => setShowInstallPrompt(false)}
              className="flex-1"
            >
              Maybe Later
            </Button>
          </div>
        </div>
      )}
      <GameBoard />
    </main>
  )
}
